/* CartPage component styles */

.container {
    max-width: 1100px;
    /* Limit container width */
    margin: auto;
    /* Center the container */
    padding: 20px;
    /* Add padding */
}

.row {
    display: flex;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    flex-wrap: wrap;
    justify-content: center;
    /* Center align items */
}

.col-md-8 {
    flex: 0 0 60%;
    /* Two-thirds width for the main content */
    max-width: 60%;
}

.col-md-4 {
    flex: 0 0 40%;
    /* One-third width for the summary section */
    max-width: 40%;
}

.btn-danger {
    padding: 4px 8px;
    font-size: 14px;
    border-radius: 5px;
    background-color: #dc3545;
    color: #fff;
    border: none;
}

.btn-danger:hover {
    background-color: #c82333;
    /* Darker background color on hover */
}

.text-center {
    text-align: center;
}

.text-right {
    text-align: right;
}

.btn-primary {
    padding: 12px 24px;
    font-size: 16px;
    border-radius: 5px;
    background-color: #007bff;
    color: #fff;
    border: none;
}

.btn-primary:hover {
    background-color: #0056b3;
    /* Darker background color on hover */
}

.btn-outline-warning {
    padding: 4px 8px;
    font-size: 14px;
    border-radius: 5px;
    border: 1px solid #ffc107;
    color: #ffc107;
    background-color: transparent;
}

.btn-outline-warning:hover {
    color: #212529;
    background-color: #ffc107;
}

hr {
    border-top: 1px solid #ccc;
    margin: 10px 0;
}

/* Responsive styles */
@media (max-width: 767px) {

    .col-md-8,
    .col-md-4 {
        flex: 0 0 100%;
        max-width: 100%;
    }

    .btn-danger,
    .btn-primary,
    .btn-outline-warning {
        width: 100%;
    }
}